const html = `<!doctype html>
<html>
<head>
<meta charset='UTF-8'><meta name='viewport' content='width=device-width initial-scale=1'>
<title>1</title></head>
<body><h1>React Hook</h1>
<h2>函数组件</h2>
<pre><code class='language-typescript' lang='typescript'>import React, { FC, useState } from &#39;react&#39;
interface IProps = {}
const Component: FC&lt;IProps&gt; = () =&gt; {
    return ()
}

export default Home
</code></pre>
<h2>Hooks</h2>
<h5>1、useState</h5>
<pre><code class='language-tsx' lang='tsx'>import React, { useState } from &#39;react&#39;

// 用数组接受 useState 返回的两项
/**
  * 数据
  *-修改数据的方法
*/
const [data, setData] = useState&lt;Type&gt;(initial)
</code></pre>
<blockquote><p><strong>当传参为对象类型时，setXxx 会覆盖旧状态，所以合并可用以下写法</strong></p>
<pre><code class='language-react' lang='react'>setXxx({
 ...Xxx,
 updateItem: xxx
})
</code></pre>
</blockquote>
<h5>2、useEffect</h5>
<ul>
<li><p>执行机制：</p>
<ul>
<li>第<mark>一</mark>个参数必须，为<mark>函数</mark></li>
<li>第<mark>二</mark>个参数可选，为<mark>数组</mark></li>
<li>在<code>render</code>的时候（<code>mount</code>，<code>update</code>）被调用</li>
<li>每次调用，会“记住”数组参数，当下一次被调用的时候，逐个比较数组中的元素，看和上一次调用的数组元素是否完全一样，如果完全一样，第一个参数就不执行，如果不一样，就执行第一个参数</li>

</ul>
</li>

</ul>
<blockquote><p><strong><code>componentDidMount</code> ， <code>componentDidUpdate</code> 和 <code>componentWillUnmount</code> 三个函数的组合</strong></p>
<ol start='' >
<li><p><code>componentDidMount</code>时执行</p>
<pre><code class='language-javascript' lang='javascript'>useEffect(() =&gt; {}, [])
</code></pre>
</li>
<li><p><code>componentDidMount</code> ， <code>componentDidUpdate</code>时都执行</p>
<pre><code class='language-javascript' lang='javascript'>useEffect(() =&gt; {})
</code></pre>
</li>
<li><p><code>componentDidMount</code> ， <code>componentDidUpdate</code>时都执行（只在某些<code>data</code>更新才执行）</p>
<pre><code class='language-javascript' lang='javascript'>useEffect(() =&gt; {}, [updateData1, updateData2, ...])
</code></pre>
</li>
<li><p><code>componentWillUnmount</code>时都执行，在1、2、3基础上加上返回函数</p>
<pre><code class='language-javascript' lang='javascript'>useEffect(() =&gt; {
    return () =&gt; {}
}, [])
</code></pre>
</li>

</ol>
</blockquote>
</body>
</html>`
export default html